import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController } from '@ionic/angular';
import * as Driver from 'driver.js';
import 'driver.js/dist/driver.min.css';

import { User } from 'src/model/user';

@Component({
  selector: 'app-boite-a-idees',
  templateUrl: './boite-a-idees.page.html',
  styleUrls: ['./boite-a-idees.page.scss'],
})
export class BoiteAIdeesPage implements OnInit {

  user:User;
  inputValue:string="";
  currentDate = new Date();
  suggestion:any;
  allSuggetions :any = [{title:'title1',date:this.currentDate,user:'admin'},{title:'title2',date:this.currentDate,user:'admin'}]
  isSuggestionActivated:boolean = true;
  isHistoricalActivated:boolean = false;
  historicalSuggesions:any = [];
  showSuggestionMessage:boolean;
  showSuggestionList:boolean;
  storageSuggestions:any=[];
    
  constructor(public alertController: AlertController,public router : Router,) {}
  
  async suggestionAlert(message) {
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
      header: 'Information',
      subHeader: '',
      message: message,
      buttons: [   
        {
          text: 'Ok',
          handler: (alertData) => {
           }
        }
      ]
    });
    await alert.present();
  }

  refreshPage(){
    let currentUrl = this.router.url;
      this.router.navigateByUrl('/', {skipLocationChange: true}).then(() => {
          this.router.navigate([currentUrl]);
      });
  }
 
  getSuggestion(){

    if(this.inputValue != ""){
      this.suggestion = {
        title:this.inputValue,
        date:this.currentDate,
        user:this.user.firstname
      };
      this.allSuggetions.push(this.suggestion);
      localStorage.setItem('suggestion', JSON.stringify(this.allSuggetions));
      this.suggestionAlert('Votre suggestion a bien été prise en compte');
      this.refreshPage();
    }else{
      this.suggestionAlert('Veuillez ajouter une idée');
    }
  }

  showSuggestionCard(){
    this.isSuggestionActivated = true;
    this.isHistoricalActivated = false;
  }

  showHistoricalCard(){
    this.isSuggestionActivated = false;
    this.isHistoricalActivated = true;
  }

  ngOnInit() {
    
    if(!JSON.parse(localStorage.getItem('suggestion'))){
      localStorage.setItem('suggestion',JSON.stringify(this.allSuggetions))
    }
    this.allSuggetions = JSON.parse(localStorage.getItem('suggestion'))
    if(!this.allSuggetions || this.allSuggetions.length ==0 ){
      this.showSuggestionMessage = true;
      this.showSuggestionList = false;
      this.allSuggetions = [];
    }else{
      this.showSuggestionMessage = false;
      this.showSuggestionList = true;
    }
    this.user = <User> {
      id:'1',    
      firstname: 'moi',
      role_description: 'admin',
    } 
    if(JSON.parse(localStorage.getItem('suggestion'))){
      if(!this.allSuggetions || this.allSuggetions.length ==0 ){

        console.log('SUGGESTION NUL')
      }else{
        this.allSuggetions.map(oneSuggestion=>{
          this.historicalSuggesions.push({
            title:oneSuggestion.title,
            date:oneSuggestion.date,
            user:oneSuggestion.user
          })
        })
      }
    }
  }
}
